  width: 400px;
  margin: auto;
  font-size: 77%;
  z-index: 99;
  opacity: 1;
}
.l-footer__banner.is-pop-close {
  height: 0;
  top: -1000px;
}
.l-footer__banner.is-active {
  position: fixed;
}
.l-footer__bannerLink {
  display: block;
  width: 300px;
  margin-bottom: 20px;
  text-decoration: none;
  color: #fff;
  border-radius: 5px;
}
.l-footer__bannerLink:hover {
  text-decoration: none;
  background: #999;
}
.l-footer__cuponCode {
  margin: auto;
  padding: 0 0.5em;
  text-align: center;
  font-size: 4rem;
  background-color: #ffd700;
}
.l-footer__bannerButton {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 50px 0 auto auto;
  padding: 0.5em 1em;
  border-radius: 10px;
  background-color: #ff6347;
  font-size: 1.8rem;
  color: #fff;
}
.l-footer__pagetop {
  line-height: 1;
  color: #1C1C1D;
  font-size: 1rem;
  font-weight: bold;
  text-align: center;
  position: fixed;
  right: 30px;
  bottom: 30px;
  z-index: 9;
  transition: 0.3s;
}
.l-footer__pagetop__icon {
  display: block;
  width: 44px;
  height: 44px;
  margin: 0 auto 10px;
  border-radius: 8px;
  background: rgba(29, 29, 29, 0.5);
  position: relative;
}
.l-footer__pagetop__icon::before {
  color: #fff;
  font-size: 3.2rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.is-pageTopHide .l-footer__pagetop, .is-scrollUp .l-footer__pagetop {
  opacity: 0;
  visibility: hidden;
}
.l-footer__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 60px;
}
.l-footer__contact {
  display: flex;
  align-items: center;
  text-align: right;
}
.l-footer__inquiry {
  margin-right: 60px;
}
.l-footer__inquiry > .snsLinks {
  justify-content: flex-end;
}
.l-footer__address {
  margin-bottom: 10px;
  font-size: 1.2rem;
  line-height: 1.5;
}
.l-footer__copyright {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  background: #FA7026;
  color: #fff;
  font-size: 1rem;
}
@media (max-width: 768px) {
  .l-footer__pagetop {
    right: 15px;
    bottom: 30px;
  }
  .l-footer__inner {
    flex-direction: column;
    padding: 15px;
  }
  .l-footer__navi {
    margin-bottom: 15px;
  }
  .l-footer__contact {
    flex-direction: column-reverse;
    text-align: center;
  }
  .l-footer__inquiry {
    margin-right: 0;
  }
  .l-footer__inquiry > .snsLinks {
    justify-content: center;
  }
  .l-footer__logo {
    width: 60px;
  }
  .l-footer__address {
    margin-top: 15px;
  }
}

.footerPaymentMethods {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 60px;
  border-bottom: solid 1px #D3D3D3;
  line-height: 1;
}
.footerPaymentMethods__item {
  display: flex;
  align-items: center;
}
.footerPaymentMethods__item:not(:first-child) {
  margin-left: 64px;
  position: relative;
}
.footerPaymentMethods__item:not(:first-child)::before {
  content: "";
  display: block;
  width: 1px;
  height: 24px;
  background: #1D1D1D;
  position: absolute;
  left: -32px;
  top: 50%;
  transform: translate(-50%, -50%) skew(-24deg);
}
.footerPaymentMethods__label {
  margin-right: 30px;
  font-size: 1.4rem;
  font-weight: bold;
  line-height: 1.25;
}
.footerPaymentMethods__detail {
  display: flex;
}
.footerPaymentMethods__method {
  margin-left: 15px;
}
.footerPaymentMethods__method:first-child {
  margin-left: 0;
}
.footerPaymentMethods__method > img {
  width: auto;
  height: 20px;
}
@media (max-width: 768px) {
  .footerPaymentMethods {
    flex-direction: column;
    height: auto;
    padding: 15px;
  }
  .footerPaymentMethods__item:not(:first-child) {
    margin-left: 0;
    margin-top: 15px;
  }
  .footerPaymentMethods__item:not(:first-child)::before {
    display: none;
  }
  .footerPaymentMethods__label {
    margin-right: 15px;
    font-size: 1.2rem;
  }
  .footerPaymentMethods__method {
    margin-left: 10px;
  }
  .footerPaymentMethods__method > img {
    height: 15px;
  }
}

.footerNavi {
  font-size: 1.4rem;
}
.footerNavi__section {
  margin-top: 15px;
}
.footerNavi__label {
  margin-bottom: 5px;
  font-size: 1.2rem;
  font-weight: bold;
}
.footerNavi__item {
  display: inline-block;
}
.footerNavi__item::before {
  content: "/";
  display: inline-block;
  margin: 0 1.5em;
}
.footerNavi__item:first-of-type::before {
  display: none;
}
@media (min-width: 769px) {
  .footerNavi__item a:hover {
    color: #FA7026;
    opacity: 1;
  }
}
@media (max-width: 768px) {
  .footerNavi {
    text-align: center;
  }
  .footerNavi__item::before {
    margin: 0 1em;
  }
}

.floatingNavi {
  display: none;
}

.floatingNavi {
  display: flex;
  position: fixed;
  left: 18pt;
  bottom: 18pt;
  z-index: 9;
  transition: 0.5s;
}
body.is-pageTopHide .floatingNavi {
  opacity: 0;
  visibility: hidden;
}
.floatingNavi__item {
  width: 64px;
  margin-left: 10px;
}
.floatingNavi__item:first-child {
  margin-left: 0;
}
.floatingNavi__button {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  width: 64px;
  height: 64px;
  padding-bottom: 5px;
  border: solid 2px;
  border-radius: 50%;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.25);
  font-size: 1rem;
  font-weight: bold;
  position: relative;
}
.floatingNavi__button--contact {
  border-color: #FCA827;
  background: #FCA827;
}
.floatingNavi__button--price {
  border-color: #E35300;
  background: #E35300;
  color: #fff;
}
@media (min-width: 769px) {
  .floatingNavi__button:hover {
    background: #fff;
    text-decoration: none;
  }
  .floatingNavi__button--price:hover {
    color: #1C1C1D;
  }
}
.floatingNavi__icon {
  margin-bottom: 2px;
  font-size: 1.8rem;
  line-height: 23px;
}
.floatingNavi__icon.icon--mail {
  font-size: 2.3rem;
}
.floatingNavi__balloon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 140px;
  height: 32px;
  border-radius: 4px;
  background: #E35300;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.25);
  color: #fff;
  position: absolute;
  top: -50px;
  right: 0;
}
.floatingNavi__balloon::after {
  content: "";
  display: block;
  border-top: solid 12px #E35300;
  border-left: solid 6px transparent;
  border-right: solid 6px transparent;
  position: absolute;
  right: 32px;
  bottom: -12px;
  transform: translate(50%, 0);
}
@media (max-width: 640px) {
  .floatingNavi__item {
    width: 44px;
  }
  .floatingNavi__button {
    width: 44px;
    height: 44px;
    padding-bottom: 0;
  }
  .floatingNavi__icon {
    margin-bottom: 0;
  }
  .floatingNavi__label {
    display: none;
  }
  .floatingNavi__balloon {
    width: 120px;
    height: 24px;
    right: -12px;
    top: -40px;
  }
  .floatingNavi__balloon::after {
    content: "";
    display: block;
    border-top: solid 10px #E35300;
    border-left: solid 5px transparent;
    border-right: solid 5px transparent;
    position: absolute;
    right: 32px;
    bottom: -10px;
  }
}